.App-header {
  margin: 100px 300px 75px 50px;
  position: absolute;
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  font-size: calc(10px + 2vmin);
  font-family: 'Oswald', sans-serif;
  color: #012b5d;
}
h1{
  font-family: 'Oswald', sans-serif;
  color: #e31837;
}
p{
  font-family: 'Oswald', sans-serif;
  font-size: 20px;
  font-weight: 200;
}

